.searchBarContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.searchBarWrapper {
  width: 80%;
}
.searchWrapper {
  display: flex;
  margin: 10px 4px;
  height: 20rem;
  width: 100%;
}
.resultingUsers {
  padding: 10px;
  width: 60%;
  border-right: 1px solid #26282e;
  height: inherit;
  overflow-y: auto;
}
.resultingUsers > div {
  border: 2px solid #008ecf;
  margin: 5px 2px;
  border-radius: 10px;
}
.selectedUsers {
  width: 40%;
  height: inherit;
  overflow-y: auto;
}
.selectedUsers > div:first-child {
  justify-content: center;
}
.createGroupForm {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.createGroupForm > input {
  margin: 10px 0;
}
.selectedUsers > div {
  margin: 5px 2px;
}
.selectedUsers > div > img {
  width: 30px;
  height: 30px;
}
.addBtn {
  border: none;
  position: absolute;
  bottom: 2px;
  z-index: 10;
  right: 0;
}
.createBtn {
  position: absolute;
  bottom: 0;
  border: 2px solid red;
}

.textAlign {
  margin: 0;
  text-align: center;
}

@media screen and (max-width: 700px) {
  .groupChatModal {
    height: 650px;
  }
  .searchWrapper {
    flex-direction: column;
    height: 80%;
  }
  .resultingUsers {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid #26282e;
  }
  .selectedUsers {
    margin-top: 4px;
    width: 100%;
  }
}
